import { registerBlockType } from '@wordpress/blocks';
import {
	InspectorControls,
	useBlockProps,
} from '@wordpress/block-editor';

import {
	ToggleControl,
	SelectControl,
	PanelBody,
} from '@wordpress/components';

const { __ } = wp.i18n;

registerBlockType('filter-plus/woo-filter', {
    title: 'WooCommerce Product Filter',
    icon: 'image-filter',
    category: 'text',
	attributes: {
		colors: {
			type: 'boolean',
		},
		size: {
			type: 'boolean',
		},
		template: {
			type: 'string',
			default: '1' ,
		},
		categories: {
			type: 'array',
			default: [] ,
		},
		show_tags: {
			type: 'boolean',
		},
		tags: {
			type: 'array',
			default: [] ,
		},
		show_attributes: {
			type: 'boolean',
		},
		attribute_list: {
			type: 'array',
			default: [] ,
		},
		show_reviews: {
			type: 'boolean',
		},
		show_price_range: {
			type: 'boolean',
		},
		sorting: {
			type: 'boolean',
		},
		product_tags: {
			type: 'boolean',
		},
		product_categories: {
			type: 'boolean',
		},
	},

	edit( { attributes, setAttributes } ) {
        const blockProps = useBlockProps();

        const {
            colors,
            size,
            template,
            categories,
            show_tags,
            tags,
			show_attributes,
			attribute_list,
			show_reviews,
			show_price_range,
			sorting,
			product_tags,
			product_categories
        } = attributes;

        function onChangeDisplayColor( newValue ) {
            setAttributes( { colors: newValue } );
        }
		function onChangeDisplaySize( newValue ) {
            setAttributes( { size: newValue } );
        }
        function onChangeCatList( newValue ) {
            setAttributes( { categories: newValue } );
        }
        function onChangeTemplate( newValue ) {
            setAttributes( { template: newValue } );
        }
		function onChangeDisplayTags( newValue ) {
            setAttributes( { show_tags: newValue } );
        }
        function onChangeTagist( newValue ) {
            setAttributes( { tags: newValue } );
        }
		function onChangeDisplayAttr( newValue ) {
            setAttributes( { show_attributes: newValue } );
        }
        function onChangeAttr( newValue ) {
            setAttributes( { attribute_list: newValue } );
        }
		function onChangeShowReviews( newValue ) {
            setAttributes( { show_reviews: newValue } );
        }
		function onChangeSorting( newValue ) {
            setAttributes( { sorting: newValue } );
        }
		function onChangeProductTag( newValue ) {
            setAttributes( { product_tags: newValue } );
        }
		function onChangeProductCat( newValue ) {
            setAttributes( { product_categories: newValue } );
        }
		function getTemplates() {
			let templates = [ { value: 1 , label: __( 'Template-1', 'filter-plus' ) }];
			if ( filterPlus?.is_pro_active == true ) {
				templates.push({ value: 2 , label: __( 'Template-2', 'filter-plus' ) });
			}

			return templates;
		}
console.log(attributes);
        return (
			<>
				<InspectorControls>
					<PanelBody title={ __( 'Settings', 'filter-plus' ) }>
						<SelectControl
							label={ __( 'Select Template:', 'filter-plus' ) }
							value={ template }
							options={ getTemplates() }
							onChange={ onChangeTemplate }
						/>
						<SelectControl
							multiple
							label={ __( 'Category List:', 'filter-plus' ) }
							value={ categories }
							options={filterPlus?.woo_categories}
							onChange={ onChangeCatList }
						/>
						<ToggleControl
							label={ __( 'Display Colors:', 'filter-plus' ) }
							checked={ colors }
							onChange={ onChangeDisplayColor }
						/>
						<ToggleControl
							label={ __( 'Display Size:', 'filter-plus' ) }
							checked={ size }
							onChange={ onChangeDisplaySize }
						/>
						{
							filterPlus?.is_pro_active == true && (
							<>
								<ToggleControl
									label={ __( 'Display Tags:', 'filter-plus' ) }
									checked={ show_tags }
									onChange={ onChangeDisplayTags }
								/>
								<SelectControl
									multiple
									label={ __( 'Tags:', 'filter-plus' ) }
									value={ tags }
									options={filterPlus?.tags}
									onChange={ onChangeTagist }
								/>
								<ToggleControl
									label={ __( 'Display Attributes:', 'filter-plus' ) }
									checked={ show_attributes }
									onChange={ onChangeDisplayAttr }
								/>
								<SelectControl
									multiple
									label={ __( 'Attributes:', 'filter-plus' ) }
									value={ attribute_list }
									options={filterPlus?.attributes}
									onChange={ onChangeAttr }
								/>
								<ToggleControl
									label={ __( 'Display Reviews:', 'filter-plus' ) }
									checked={ show_reviews }
									onChange={ onChangeShowReviews }
								/>
								<ToggleControl
									label={ __( 'Display Sorting:', 'filter-plus' ) }
									checked={ sorting }
									onChange={ onChangeSorting }
								/>
								<ToggleControl
									label={ __( 'Display Tags in Filter Result:', 'filter-plus' ) }
									checked={ product_tags }
									onChange={ onChangeProductTag }
								/>
								<ToggleControl
									label={ __( 'Display Categories in Filter Result:', 'filter-plus' ) }
									checked={ product_categories }
									onChange={ onChangeProductCat }
								/>
							</>
						)}
					</PanelBody>
				</InspectorControls>

				<div { ...blockProps }>
					This is the editor text!
				</div>
			</>
		);
	},

	save( { attributes } ) {
		const {
            colors,
            size,
            template,
            categories,
            show_tags,
            tags,
			show_attributes,
			attribute_list,
			show_reviews,
			show_price_range,
			sorting,
			product_tags,
			product_categories
		} = attributes;

		const blockProps = useBlockProps.save();

		return (
			<div {...blockProps}>
			</div>
		);
	},

} );
